@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,500,700&display=swap);
body {
  /* background: linear-gradient(180deg, rgba(248,177,149,1) 0%, rgba(192,108,132,1) 35%, rgba(53,92,125,1) 100%) */
  padding: 2rem;
  background: rgb(27, 27, 27);
  font-family: 'Work Sans', sans-serif;
  color: #fff;
}
.imageSearch {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
  position: relative;
}

.imageSearch__form input {
  margin: 0 auto;
  background-color: transparent;
  border: transparent;
  border-bottom: solid 2px #34495e;
  padding-bottom: 8px;
  width: 25%;
  font-weight: 200;
  letter-spacing: 2px;
  font-size: 16px;
  color: #fff;
  margin-right: 10px;
}

.imageSearch__form input:focus {
  outline: none;
}

.imageSearch__form button {
  border: 0;
  width: 7%;
  position: absolute;
  top: 4px;
  background: transparent;
  border: solid 2px #34495e;
  color: #fff;
}
.imageList__container {
  border-radius: 1px;
  box-shadow: -1px 2px 34px -13px rgba(0,0,0,0.55);
  text-align: left;
  overflow: hidden;
}

.imageList__image {
  width: 100%;
  height: 200px;
}

.image__details {
  background: #34495e;
}

.image__details button {
  width: 100%;
  border: 0;
  background-color: #2c3e50;
  color: #fff;
}
.imageView__container {
  width: 100%;
  text-align: center;
  color: #fff;
}

.imageView__img {
  height: 400px;
  width: auto;
}

.imageView__copyright {
  color: #8f8f8f;
}

.imageView__text {
  margin-top: 1rem;
}

.imageView__text h4, h5, span {
  letter-spacing: 1px;
  color: #f39c12;
}

.imageView__text h4 span {
  font-weight: bold;
  color: #c0392b;
}

.imageView__text a {
  font-weight: bold;
  color: #c0392b;
}

.imageView__text button {
  border: 0;
  background: transparent;
  border: solid 2px #34495e;
  margin-top: 10px;
}
